* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  background-image: radial-gradient(#808080, 1px, transparent 0);
  background-size: 40px 40px;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  margin-left: 40px;

}

#search-box:focus, #search-box:active {
outline: none;
border: 1px solid #6733ae;


}


.country-info{
  margin-left: 40px;
  
}
.country-name{
margin-bottom: 35px;

}

.country-data{
  font-size: 15px;
  font-weight: 700;
}

li {
  list-style: none;

}

.new-size{
  font-size: 25px;
  font-weight: 700;
}


